.Header {
  display: flex;
  padding: 0.5rem 1rem 0rem 1rem;
  background-color: rgba(50, 118, 186, 0.9);
  outline: 1px solid pink;
}
.Header img {
  width: 18vw;
}
.Header nav {
  font-size: 1.5vw;
  color: #FFFFFF;
  font-weight: 600;
  outline: 2px solid yellow;
  margin-left: auto;
}
.Header nav ul {
  display: flex;
  gap: 1.5rem;
  list-style: none;
}
.Header nav ul li ul {
  background-color: #000000;
  color: #FFFFFF;
  z-index: 99;
}
.AboutHi {
  transform: translateY(100%);
  opacity: 0;
}
.AboutSh {
	font-size: 1vw;
  position: absolute;
  display: flex;
  flex-direction: column;
  transition: ease 1000ms;
}
.AboutHi li {
  opacity: 0;
  position: absolute;
  transform: translateY(100%);
}
.AboutSh li {
	margin: 0.5rem 0.5rem 0.5rem -1.8rem;
  transition: all 500ms;
}
/* PRODUCTS PRODUCTS*/
.ProductHi {
  transform: translateY(100%);
  opacity: 0;
}
.ProductSh {
	font-size: 1vw;
  position: absolute;
  display: flex;
  flex-direction: column;
  transition: ease 500ms;
}
.ProductHi li {
  opacity: 0;
  position: absolute;
  transform: translateY(100%);
}
.ProductSh li {
	margin: 0.5rem 0.5rem 0.5rem -1.8rem;
  transition: all 500ms;
}
/* Expand */
ul li ul li:nth-child(1) {
  transition-delay: 200ms;
}
ul li ul li:nth-child(2) {
  transition-delay: 400ms;
}
ul li ul li:nth-child(3) {
  transition-delay: 600ms;
}
a {
  color: inherit;
  text-decoration: none;
}
.bg {
  font-size: 5vw;
}
