.Footer {
  outline: 3px solid green;
  position: relative;
  height: 100%;
  display: grid;
  grid-template-columns: 33.33% 33.33% 33.33%;
  grid-template-rows: 33.33% 33.33% 33.33%;
}
@media only screen and (max-width: 600px) {
	.Footer {
	display: flex; 
	flex-direction: column;
	}
}
.foot-item {
  outline: 4px solid green;
  display: flex;
  flex-direction: column;
  padding: 0rem 1rem;
  align-items: left;
}
.foot-item p {
  font-size: 1rem;
  color: cyan;
}
.foot-item a {
  margin-bottom: 0.5rem;
  color: orange;
}
.foot-item-1 {
  /* About us */
  grid-column: 1 / 2;
  grid-row: 1 / 4;
}
.foot-item-2 {
  /* Products */
  grid-column: 2 / 3;
  grid-row: 1 / 4;
}
.foot-item-3 {
  /* Contact Us */
  grid-column: 3 / 4;
  grid-row: 1 / 4;
}
