/* grid container */
.Rajesh {
  display: grid;
  margin: 0vw 16vw 0vw 14vw;
  min-height: 32vh;
  grid-template-columns: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;
  grid-template-rows: 10% 10% 10% 10% 10% 10% 10% 10% 10% 10%;
}
.grid-item {
  outline: 4px solid green;
}
.Rajesh .grid-item img {
  max-width:100%;
  max-height:100%;
}
.grid-item-1 {
  /* Sunshine ra Gyalva */
  grid-column: 1 / 11;
  grid-row: 1 / 4;
  display: flex;
  gap: 2rem;
  padding: 0rem 1rem;
  justify-items: center;
  align-items: center;
}
.SuperHi {
  transform: translateX(-100%);
  opacity: 0;
  width: 45%;
  height: 50%;
}
.SuperSh {
  transition: ease 500ms;
  margin-right: auto;
  outline: 4px dotted green;
  width: 45%;
  height: 50%;
}
.GyalvaHi {
  transform: translateX(100%);
  opacity: 0;
  width: 40%;
  height: 50%;
}
.GyalvaSh {
  transition: ease 500ms;
  width: 40%;
  height: 50%;
  margin-left: auto;
  outline: 4px dotted green;
}
.grid-item-2 {
  /* Rajesh hamal */
  display: flex;
  grid-column: 1 / 4;
  grid-row: 5 / 11;
  z-index: 9;
}
.RajeshHi {
  transform: translateX(-100%);
  opacity: 0;
}
.RajeshSh {
  transition: ease 1000ms;
}
.grid-item-3 {
  /* Madhu */
  outline: 3px solid red;
  display: flex;
  grid-column: 9 / 11;
  grid-row: 5 / 11;
  z-index: 5;
}
.MadhuHi {
  transform: translateX(100%);
  opacity: 0;
}
.MadhuSh {
  transition: ease 1000ms;
}
.grid-item-4 {
  /* Jasta */
  grid-column: 5 / 8;
  grid-row: 4 / 11;
  z-index: 1;
}
.grid-item-5 {     /* Ko Saath */
  display: flex;
  grid-column: 2 / 10;
  grid-row: 7 / 11;
  z-index: 2;
}
#animate {
  position: absolute;
  margin-top: 700px;
  font-size: 4rem;
}